import * as React from "react";
import Config from "../config";
import api from "../api";
import $ from "jquery";
import _ from "lodash";

export default class createMilestone extends React.Component {

    constructor(props) {
        super(props);
        this.repository = Config.repository;
        this.fetchIssue();
    }

    async fetchIssue() {
        await Config.repoUrl.map(async (value) => {
            let issueListRepo = await api.getIssueList(value.projectId);
            let data = await issueListRepo.json();
            data.map((value) => {
                let labels = value.labels;
                let estimate = value.time_stats.time_estimate / 3600;
                let severity = 9999;
                let priority = 9999;
                let severityRegex = new RegExp("S+[0-9]*");
                let priorityRegex = new RegExp("P+[0-9]*");

                labels.map((label) => {
                    if (severityRegex.test(label)) {
                        severity = parseFloat(label.replace('S', ''), 10);
                    }
                    if (priorityRegex.test(label)) {
                        priority = parseFloat(label.replace('P', ''), 10);
                    }
                });

                this.repository.map((valueRepo, index) => {
                    if (typeof valueRepo.issueList == 'undefined') {
                        valueRepo.issueList = [];
                    }
                    if (valueRepo.projectId === value.project_id && value.title.includes('['+valueRepo.release_branch+']')) {
                        let valueIssue = valueRepo.issueList;
                        valueIssue.push({
                            id: value.id,
                            iid: value.iid,
                            title: value.title,
                            estimate: estimate,
                            severity: severity,
                            priority: priority,
                            labels: labels,
                            projectId: value.project_id
                        });
                        valueRepo.issueList = valueIssue;
                    }
                    return valueRepo;
                });

            });

        });
        return this.repository;
    }

    assignIssueToNextMileStone = async () =>
    {
        let self = this;
        this.repository.map(async (repos, index) => {
            let effortPercent = 0;
            if ($('#' + repos.value + '-repo-effort').val()) {
                effortPercent = parseFloat($('#' + repos.value + '-repo-effort').val());
            }

            let hour = parseFloat($('#time_total').val());
            let timeForLineProduct = hour * effortPercent / 100;
            let issueForLine = self.sortIssueInArray(repos.issueList);

            let issueNeedAssign = [];

            let nextFourMileStone = await self.getNextFourMileStone(repos);

            if (nextFourMileStone) {
                let nextFourMileStoneTitleArray = [];
                for(let i = 0; i < nextFourMileStone.length; i++) {
                    nextFourMileStoneTitleArray.push(nextFourMileStone[i].title);
                }

                let openedMileStone = await api.getListMileStoneOpened(repos);
                let openedMileStoneResult = await openedMileStone.json();
                let openedMileStoneTitleArray = [];
                openedMileStoneResult.map((value, index) => {
                    if (value.title.includes('v'+ repos.release_branch)) {
                        openedMileStoneTitleArray.push(value.title);
                    }
                });

                let difference = nextFourMileStoneTitleArray
                    .filter(x => !openedMileStoneTitleArray.includes(x))
                    .concat(openedMileStoneTitleArray.filter(x => !nextFourMileStoneTitleArray.includes(x)));

                for(let i = 0; i < difference.length; i++) {
                    let value = difference[i];
                    let duedate = '';
                    nextFourMileStone.map((value) => {
                       if (value.title === difference[i] && !duedate) {
                           duedate = value.dueDate;
                       }
                    });
                    await api.createMilestone(repos.projectId, {
                        title: value,
                        description: value,
                        due_date: duedate, //to calculate later
                    });
                }

                for(let i = 0; i < nextFourMileStoneTitleArray.length; i++) {
                    let milestoneTitle = nextFourMileStoneTitleArray[i];
                    let milestoneRequest = await api.getIssueVerifiedFromMileStone(repos.projectId, milestoneTitle);
                    let milestoneRequestResult = await milestoneRequest.json();
                    let searchMilestoneRequest = await api.searchMileStoneByTitle(repos, milestoneTitle);
                    let searchMilestoneRequestResult = await searchMilestoneRequest.json();
                    let milestoneId = searchMilestoneRequestResult[0].id;
                    let totalTime = 0;
                    for(let i = 0; i < milestoneRequestResult.length; i++) {
                        let issue = milestoneRequestResult[i];
                        let estimate = issue.time_stats.time_estimate / 3600;
                        totalTime = totalTime + estimate;
                    }
                    for(let i = 0; i < issueForLine.length; i++) {
                        if (totalTime + issueForLine[i].estimate <= timeForLineProduct) {
                            totalTime = totalTime + issueForLine[i].estimate;
                            await api.updateIssueToMilestone(issueForLine[i].iid, repos.projectId, milestoneId);
                            _.remove(issueForLine, function(e) {
                                return e.iid === issueForLine[i].iid;
                            });
                        }
                    }
                }


            }

        })
    };

    sortIssueInArray(issueList) {
        return _.sortBy( issueList, ['severity', 'priority'] );
    }

    getNextFourMileStone = async (value) => {
        let openedMileStone = await api.getListMileStoneClosed(value);
        let openedMileStoneResult = await openedMileStone.json();
        let lastestClosed = '';
        openedMileStoneResult.map(
            (milestone) => {
                if (milestone.title.includes('v' + value.release_branch)) {
                    if (!lastestClosed) {
                        lastestClosed = milestone;
                    }
                }
            }
        );
        if (lastestClosed) {
            let lastestClosedTitle = lastestClosed.title;
            let sprint1 = this.getNextVersion(lastestClosedTitle, value);
            let sprint2 = this.getNextVersion(sprint1, value);
            let sprint3 = this.getNextVersion(sprint2, value);
            let sprint4 = this.getNextVersion(sprint3, value);

            let sprint1DueDateDateTime = this.getDueDateSprint1();
            let sprint2DueDateDateTime = this.getFridayNext(sprint1DueDateDateTime);
            let sprint3DueDateDateTime = this.getFridayNext(sprint2DueDateDateTime);
            let sprint4DueDateDateTime = this.getFridayNext(sprint3DueDateDateTime);

            return [
                {
                    title: sprint1,
                    dueDate: sprint1DueDateDateTime
                },
                {
                    title: sprint2,
                    dueDate: sprint2DueDateDateTime
                },
                {
                    title: sprint3,
                    dueDate: sprint3DueDateDateTime
                },
                {
                    title: sprint4,
                    dueDate: sprint4DueDateDateTime
                }
            ];
        } else {
            return [];
        }

    }

    getNextVersion(lastestClosedTitle, repos) {
        let version = lastestClosedTitle.slice(lastestClosedTitle.indexOf(repos.release_branch), lastestClosedTitle.length);
        let analyzeVersion = version.split('.');
        analyzeVersion[2] = parseFloat(analyzeVersion[2]) + 1;
        let releaseNextVersion = lastestClosedTitle.slice(0, lastestClosedTitle.indexOf(repos.release_branch)) + analyzeVersion.join('.');
        return releaseNextVersion;
    }

    getDueDateSprint1() {
        let today = new Date();
        let day = today.getDay();
        let nextFriday = new Date();
        if (day <= 5) {
            nextFriday.setDate(today.getDate() + (5 - day));
        } else {
            nextFriday.setDate(today.getDate() + 5 + (day - 5));
        }
        nextFriday.setHours(17);
        nextFriday.setMinutes(0);
        nextFriday.setSeconds(0);
        return nextFriday;
    }

    getFridayNext(date) {
        let dateNew = new Date();
        dateNew.setDate(date.getDate() + 7);
        dateNew.setHours(17);
        dateNew.setMinutes(0);
        dateNew.setSeconds(0);
        return dateNew;

    }

    render() {
        return (
            <React.Fragment>
                <div className={'container'}>
                    <div className="form-group">
                        <label htmlFor="time_total" className={'font-weight-bold'}>Total Time For 1 Sprint</label>
                        <input type="text" className="form-control" id="time_total" name="time_total"/>
                    </div>
                    {this.repository.map((value, index) => {
                        return (
                            <div key={'form-check' + index} className="form-group repo-effort">
                                <label htmlFor={value.value + '-repo-effort'} className={'font-weight-bold'}>{value.label} (Effort %)</label>
                                <input type="text" className="form-control" id={value.value + '-repo-effort'} name="time_total"/>
                            </div>
                        )
                    })}

                    <div className="form-group">
                        <button className="btn btn-primary" onClick={this.assignIssueToNextMileStone}>Assign Issue To Milestone For 1 month</button>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}


